import { NgModule } from '@angular/core';
import { CommonModule }      from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { AddContractorComponent } from './add-contractor.component';
import { ContractorCrudRoutingModule } from './add-contractor-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';
import {CheckboxModule,ToggleButtonModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    ContractorCrudRoutingModule,
    DialogModule,
    EditorModule,
    BusyModule,
    CheckboxModule,
    ToggleButtonModule
  ],
  declarations: [
    AddContractorComponent
  ]
})
export class AddContractorModule { }



